import { ValidationOptions, registerDecorator } from "class-validator";
import { PaymentGatewayExistsRule } from "./payment-gateway-exists.rule";

export function PaymentGatewayExists(model: any, validationOptions?: ValidationOptions) {
  return function (object: any, propertyName: string) {
    registerDecorator({
      name: 'PaymentGatewayExists',
      target: object.constructor,
      propertyName: propertyName,
      constraints: [model],
      options: validationOptions,
      validator: PaymentGatewayExistsRule,
    });
  };
}